<template>
    <form @submit.prevent="onSubmit">
        <div>
            <label>Edit Name for {{ label }}</label>
            <input type="text" v-model.lazy.trim="newLabel"  ref="labelInput"/>
        </div>
        <div class="btn-group">
            <button type="button" class="btn" @click="onCancel">Cancel</button>
            <button type="submit" class="btn">Save</button>
        </div>
    </form>
</template>


<script>
export default {
    name: 'TodoEdit',
    props: {
        label: { type: String, required: true },
        id: { type: String, required: true }
    },
    data() {
        return { newLabel: this.label };
    },
    methods: {
        onSubmit() {
            if (this.newLabel && this.newLabel !== this.label) {
                console.log("TodoEdit onSubmit:", this.newLabel)
                this.$emit("item-edited", this.newLabel)
            }
        },
        onCancel() {
            console.log("TodoEdit onCancel")
            this.$emit("edit-cancelled");
        }
    },
    mounted(){
            console.log("TodoEdit mounted")
            const labelInputRef = this.$refs.labelInput
            labelInputRef.focus()
        }


}
</script>

